const csvHeaders = {
  "DATA PROCESSAMENTO RIPPER": { isnumber: false },
  "DATA DE REFERENCIA PROCESSADA": { isnumber: false },
  "ID DO BRT": { isnumber: true },
  "BRT": { isnumber: false },
  "ID DA VIAGEM BILHETAGEM": { isnumber: true },
  "ID DA PERNA BILHETAGEM": { isnumber: true },
  "ID DO USUÁRIO": { isnumber: true },
  "SEQUÊNCIA DE UTILIZAÇÃO": { isnumber: false },
  "SEQUÊNCIA DE DÉBITO": { isnumber: false },
  "TERMINAIS RELACIONADOS": { isnumber: false },
  "TARIFA LINHA": { isnumber: true },
  "PASSAGEIRO EQUIVALENTE": { isnumber: true },
  "LINHA ID": { isnumber: true },
  "LINHA CODIFICAÇÃO": { isnumber: false },
  "EMPRESA EXCLUSIVIDADE": { isnumber: true },
  "EMPRESA OPERADORA": { isnumber: true },
  "TOTAL PASSAGEIROS PAGANTES DINHEIRO": { isnumber: true },
  "DEMANDA TOTAL": { isnumber: true },
  "RECEITA": { isnumber: true },
  "CLASSIFICACAO DE VIAGEM": { isnumber: false },
  "TIPO DE REGRA": { isnumber: false }
}

/**
 * Convert os numeros do CSV do Ripper em numeros js
 * @param {Array} csvArr 
 */
function convertCSVRipperToNumber(csvArr) {
  // O primeiro array do data são os headers
  const headers = csvArr[0]; 
  const idxsNumber = headers
    .map((h, idx) => csvHeaders[h].isnumber ? idx : -1)
    .filter(h => h !== -1 );

  console.info('Indexes da colunas convertidas para número:', idxsNumber)
  
  // Os dados são todos arrays exceto o primeiro
  return csvArr.map((el, idx) => {
    if(idx === 0) {
      // Nao altera o HEADER
      return el;
    }
    // Faz a conversao para numeros dessa posicoes do csv
    for (const idxNumber of idxsNumber) {
      // So faz a conversao se o e[idxNumber] não for nulo ou vazio
      el[idxNumber] = el[idxNumber] && parseFloat(el[idxNumber].replaceAll(',', '.'));
    }
    return el;
  })
}

export default { convertCSVRipperToNumber, csvHeaders }